	module.exports = {"data":{"package":{"id":"bs-downshift","score":0.39294923250354186,"name":"bs-downshift","version":"0.1.0","description":"BuckleScript bindings for Downshift","keywords":["react","downshift","bucklescript"],"license":"MIT","updated":"2018-01-03T10:43:56.717Z","stars":0,"type":"published","quality":0.4960588905374387,"popularity":0.03086179891618843,"maintenance":0.666656959204698,"readme":"<h1><code>bs-downshift</code></h1>\n<p><a href=\"https://github.com/bucklescript/bucklescript\">BuckleScript</a> bindings for <a href=\"https://github.com/paypal/downshift\">Downshift</a></p>\n<p><a href=\"https://npmjs.org/bs-downshift\"><img src=\"https://img.shields.io/npm/v/bs-downshift.svg\" alt=\"npm\"></a>\n<a href=\"https://github.com/reasonml-community/bs-downshift/issues\"><img src=\"https://img.shields.io/github/issues/reasonml-community/bs-downshift.svg\" alt=\"Issues\"></a>\n<a href=\"\"><img src=\"https://img.shields.io/github/last-commit/reasonml-community/bs-downshift.svg\" alt=\"Last Commit\"></a>\n<a href=\"\"><img src=\"https://img.shields.io/github/size/reasonml-community/bs-downshift/lib/js/src/Fetch.js.svg\" alt=\"Size\"></a></p>\n<h2>Demo</h2>\n<p><em>Coming soon</em></p>\n<h2>Install and setup</h2>\n<h4>yarn</h4>\n<pre><code class=\"language-bash\">$ yarn add bs-downshift\n</code></pre>\n<h4>bsconfig</h4>\n<p>Add <code>bs-downshift</code> to your <code>bs-dependencies</code>: <strong>bsconfig.json</strong></p>\n<pre><code class=\"language-json\">\"bs-dependencies\": [\n  \"bs-downshift\",\n  \"reason-react\"\n]\n</code></pre>\n<h2>Usage</h2>\n<p>See <a href=\"#examples\">examples</a> folder.</p>\n<h3>Usage of <code>render</code> function</h3>\n<p>The <code>render</code> prop is a function that passes an object which contains <em>methods</em> and <em>values</em>.</p>\n<p>In ReasonML, this object is represented as a module called <code>ControllerStateAndHelpers</code>.</p>\n<p>To make use of the <em>methods</em> and <em>values</em> within that module, you need to call it by passing the argument type (which is of type <code>ControllerStateAndHelpers.t</code> and does effectively the binding to the JS object method) like:</p>\n<pre><code class=\"language-js\">render=(\n  t => {\n    ControllerStateAndHelpers.toggleMenu(t, ());\n    /* ... */\n  }\n)\n</code></pre>\n<p>You can see that in the <a href=\"#examples\">examples</a> folder.</p>\n","homepageUrl":"https://github.com/reasonml-community/bs-downshift#readme","repositoryUrl":"https://github.com/reasonml-community/bs-downshift","npmUrl":"https://www.npmjs.com/package/bs-downshift","issuesUrl":"https://github.com/reasonml-community/bs-downshift/issues","slug":"packages/bs-downshift"}},"pathContext":{"id":"bs-downshift"}}

